  html:not(.no-js) [data-aos^=zoom][data-aos^=zoom].aos-animate {
    opacity: 1;
    transform: translate3d(0, 0, 0) scale(1);
  }
  html:not(.no-js) [data-aos=zoom-in] {
    transform: scale(0.6);
  }
  html:not(.no-js) [data-aos=zoom-in-up] {
    transform: translate3d(0, 100px, 0) scale(0.6);
  }
  html:not(.no-js) [data-aos=zoom-in-down] {
    transform: translate3d(0, -100px, 0) scale(0.6);
  }
  html:not(.no-js) [data-aos=zoom-in-right] {
    transform: translate3d(-100px, 0, 0) scale(0.6);
  }
  html:not(.no-js) [data-aos=zoom-in-left] {
    transform: translate3d(100px, 0, 0) scale(0.6);
  }
  html:not(.no-js) [data-aos=zoom-out] {
    transform: scale(1.2);
  }
  html:not(.no-js) [data-aos=zoom-out-up] {
    transform: translate3d(0, 100px, 0) scale(1.2);
  }
  html:not(.no-js) [data-aos=zoom-out-down] {
    transform: translate3d(0, -100px, 0) scale(1.2);
  }
  html:not(.no-js) [data-aos=zoom-out-right] {
    transform: translate3d(-100px, 0, 0) scale(1.2);
  }
  html:not(.no-js) [data-aos=zoom-out-left] {
    transform: translate3d(100px, 0, 0) scale(1.2);
  }
  html:not(.no-js) [data-aos^=slide][data-aos^=slide] {
    transition-property: transform;
    visibility: hidden;
  }
  html:not(.no-js) [data-aos^=slide][data-aos^=slide].aos-animate {
    visibility: visible;
    transform: translate3d(0, 0, 0);
  }
  html:not(.no-js) [data-aos=slide-up] {
    transform: translate3d(0, 100%, 0);
  }
  html:not(.no-js) [data-aos=slide-down] {
    transform: translate3d(0, -100%, 0);
  }
  html:not(.no-js) [data-aos=slide-right] {
    transform: translate3d(-100%, 0, 0);
  }
  html:not(.no-js) [data-aos=slide-left] {
    transform: translate3d(100%, 0, 0);
  }
  html:not(.no-js) [data-aos^=flip][data-aos^=flip] {
    backface-visibility: hidden;
    transition-property: transform;
  }
  html:not(.no-js) [data-aos=flip-left] {
    transform: perspective(2500px) rotateY(-100deg);
  }
  html:not(.no-js) [data-aos=flip-left].aos-animate {
    transform: perspective(2500px) rotateY(0);
  }
  html:not(.no-js) [data-aos=flip-right] {
    transform: perspective(2500px) rotateY(100deg);
  }
  html:not(.no-js) [data-aos=flip-right].aos-animate {
    transform: perspective(2500px) rotateY(0);
  }
  html:not(.no-js) [data-aos=flip-up] {
    transform: perspective(2500px) rotateX(-100deg);
  }
  html:not(.no-js) [data-aos=flip-up].aos-animate {
    transform: perspective(2500px) rotateX(0);
  }
  html:not(.no-js) [data-aos=flip-down] {
    transform: perspective(2500px) rotateX(100deg);
  }
  html:not(.no-js) [data-aos=flip-down].aos-animate {
    transform: perspective(2500px) rotateX(0);
  }
}
body {
  font-family: "Urbanist", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
  color: #222;
}

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  font-family: "Reem Kufi", sans-serif;
  font-optical-sizing: auto;
  font-weight: 500;
  font-style: normal;
  text-transform: uppercase;
  letter-spacing: 0.2em;
}

h1,
.h1,
h2,
.h2 {
  font-size: 1.75rem;
  margin-bottom: 1.5rem;
}

h3 {
  font-size: 1.375rem;
  margin-bottom: 1rem;
}

.image-fill img {
  width: 100%;
  object-fit: cover;
  height: 100%;
}

p,
ol,
ul,
table {
  font-size: 1.125rem;
  line-height: 1.5;
  color: #666;
  margin-bottom: 1rem;
}

.bg-dark p,
.bg-dark ol,
.bg-dark ul,
.bg-dark table,
.bg-image p,
.bg-image ol,
.bg-image ul,
.bg-image table,
.bg-custom p,
.bg-custom ol,
.bg-custom ul,
.bg-custom table {
  color: white;
}

.bg-light p,
.bg-light ol,
.bg-light ul,
.bg-light table {
  color: #666;
}

.award {
  width: 150px;
}
@media screen and (max-width: 768px) {
  .award {
    display: none;
  }
}

.bg-dark,
.bg-image,
.bg-custom,
.bg-gradient {
  color: white;
}

.bg-dark {
  background-color: #222;
}

.bg-light {
  background-color: white;
  color: #222;
}

.bg-gradient {
  background: rgb(192, 80, 94);
  background: linear-gradient(308deg, rgb(192, 80, 94) 0%, rgb(244, 151, 11) 44%, rgb(244, 151, 11) 55%, rgb(192, 80, 94) 100%);
}

.boxed-style {
  border: 2px solid #ffffff;
}

.button {
  border-radius: 500px;
  font-family: "Reem Kufi", sans-serif;
  font-optical-sizing: auto;
  font-size: 1.125rem;
  font-weight: 700;
  font-style: normal;
  text-transform: uppercase;
  padding: 0.8rem 2rem;
  display: inline-block;
  position: relative;
}
.button > span {
  color: #222;
  position: relative;
  z-index: 2;
  transition: color 0.3s ease;
}
.button .button__bg {
  top: 0;
  left: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  background: #ffffff;
  border-radius: 500px;
  overflow: hidden;
  transition: transform 0.4s cubic-bezier(0.1, 0, 0.3, 1);
}
.button .button__bg:before, .button .button__bg:after {
  content: "";
  display: block;
  position: absolute;
}
.button .button__bg:before {
  width: 110%;
  height: 0;
  padding-bottom: 110%;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  background-color: #f39200;
  transform: translate3d(-50%, -50%, 0) scale3d(0, 0, 1);
}
.button .button__bg:after {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 0.3s;
  background-color: #f39200;
}
.button:hover > span {
  color: #ffffff;
}
.button:hover .button__bg {
  transform: scale3d(1.1, 1.1, 1);
}
.button:hover .button__bg:before {
  opacity: 1;
  transform: translate3d(-50%, -50%, 0) scale3d(1, 1, 1);
  transition: transform 0.4s cubic-bezier(0.1, 0, 0.3, 1);
}
.button:hover .button__bg:after {
  opacity: 1;
  transition-duration: 0.01s;
  transition-delay: 0.3s;
}

.bg-light .button .button__bg {
  background-color: #f39200;
}
.bg-light .button .button__bg:before {
  background-color: #222;
}
.bg-light .button .button__bg:after {
  background-color: #222;
}
.bg-light .button > span {
  color: #ffffff;
}

.bg-gradient .button .button__bg {
  background-color: #ffffff;
}
.bg-gradient .button .button__bg:before {
  background-color: #222;
}
.bg-gradient .button .button__bg:after {
  background-color: #222;
}
.bg-gradient .button > span {
  color: #222;
}
.bg-gradient .button:hover > span {
  color: #ffffff;
}

.card {
  position: relative;
  overflow: hidden;
}
.card img {
  transition: transform 0.4s ease;
}
.card .card__overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  color: #f39200;
  background-color: rgba(34, 34, 34, 0.8);
  transition: all 0.4s ease;
  font-family: "Reem Kufi", sans-serif;
  font-optical-sizing: auto;
  font-weight: 700;
  text-transform: uppercase;
  padding: 2rem;
  letter-spacing: 0.5rem;
  font-size: 1.125rem;
  text-align: center;
}
.card:hover img, .card:focus img {
  transform: scale(1.1);
}
.card:hover .card__overlay, .card:focus .card__overlay {
  opacity: 1;
}

#content {
  overflow: hidden;
}

#footer {
  background-color: #222;
  padding: 4rem 0;
}
#footer p {
  color: #ffffff;
}
#footer .footer__copy p {
  margin: 0;
}
#footer .charity {
  display: inline-block;
  max-width: 320px;
}

form {
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}
form p {
  font-size: 14px;
}
form .form-row {
  display: flex;
  margin: 0 -15px;
}
form .form-row .form-group {
  flex: 1;
  padding: 0 15px;
  padding-bottom: 0.5rem;
}
form .form-row .form-group input {
  width: 100%;
  height: 48px;
  border-radius: 500px;
  color: #222;
  padding: 1rem;
}
form label {
  margin-bottom: 0.2rem;
  font-size: 18px;
  display: inline-block;
  font-weight: 500;
}

.header {
  position: relative;
  z-index: 99;
}
.header > .container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
}

.hero {
  background-color: #3c3c3b;
  padding-top: 20rem;
  padding-bottom: 4rem;
  background-position: top center;
  background-size: cover;
  background-repeat: no-repeat;
  color: white;
}
.hero--creation {
  position: relative;
}
.hero--creation:after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  opacity: 0.8;
  background-color: #222;
  z-index: 1;
  mix-blend-mode: multiply;
}
.hero .container {
  position: relative;
  z-index: 2;
}
.hero__subtitle {
  display: inline-block;
}
.hero h1 {
  color: white;
  font-size: 3.625rem;
  font-weight: 700;
}

.icon {
  max-width: 100px;
  margin-left: auto;
  margin-right: auto;
}
.icon--lg {
  max-width: 180px;
}

.layout .layout__block.bg-image {
  background-size: cover;
  background-repeat: no-repeat;
}
.layout .layout__block.bg-dark, .layout .layout__block.bg-image {
  color: white;
}

.logo {
  width: 240px;
  max-width: 100%;
  display: inline-block;
  position: relative;
  z-index: 12;
}
@media screen and (max-width: 768px) {
  .logo {
    width: 180px;
  }
}

.hamburger {
  position: relative;
  z-index: 99;
}

.nav-main {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow-x: hidden;
  visibility: hidden;
  transition-delay: 0.6s;
}
.nav-main:before {
  content: "";
  display: block;
  position: fixed;
  clip-path: circle(0% at 100% 0%);
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 10;
  background-color: #f39200;
  transition: clip-path 0.6s ease-in-out;
}
.nav-main .menu {
  position: relative;
  z-index: 11;
  text-align: center;
  padding-top: 8rem;
  display: flex;
  flex-direction: column;
  margin: 0;
}
.nav-main .menu li {
  animation-name: animateOut;
  animation-duration: 250ms;
  animation-delay: 0;
  animation-fill-mode: both;
  animation-timing-function: ease;
}
.nav-main .menu li + li {
  margin-top: 1.5rem;
}
@media (max-width: 992px) {
  .nav-main .menu li + li {
    margin-top: 1rem;
  }
}
.nav-main .menu li a {
  font-family: "Reem Kufi", sans-serif;
  font-optical-sizing: auto;
  font-weight: 500;
  color: white;
  text-transform: uppercase;
  font-size: 1.875rem;
  display: inline-block;
  letter-spacing: 0.2em;
}
@media (max-width: 992px) {
  .nav-main .menu li a {
    font-size: 1.5rem;
  }
}
.nav-main .menu li .menu__dropdown {
  margin: 0;
  margin-top: 0.5rem;
}
@media (max-width: 992px) {
  .nav-main .menu li .menu__dropdown {
    margin-top: 0.2rem;
  }
}
.nav-main .menu li .menu__dropdown li a {
  font-size: 1.125rem;
}
@media (max-width: 992px) {
  .nav-main .menu li .menu__dropdown li a {
    font-size: 1rem;
  }
}
.nav-main .menu li .menu__dropdown li + li {
  margin-top: 0.5rem;
}
@media (max-width: 992px) {
  .nav-main .menu li .menu__dropdown li + li {
    margin-top: 0.1rem;
  }
}
.nav-main.is-open {
  visibility: visible;
  transition-delay: 0s;
}
.nav-main.is-open .menu {
  visibility: visible;
  opacity: 1;
}
.nav-main.is-open .menu li {
  animation-name: animateIn;
  animation-delay: calc(var(--animation-order) * 50ms + 200ms);
}
.nav-main.is-open:before {
  transition: clip-path 0.8s ease-in-out;
  clip-path: circle(150%);
}

@keyframes animateIn {
  0% {
    opacity: 0;
    transform: translateY(-2rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes animateOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: scale(0.8) translateY(0);
  }
}
.parallax {
  position: relative;
}
.parallax > * {
  position: relative;
  z-index: 1;
}
.parallax .parallax__image {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
  object-fit: cover;
  z-index: 0;
}

.social {
  display: flex;
}
.social li {
  margin-right: 0.7rem;
}
.social li a svg {
  width: 34px;
  height: 34px;
}
.social li a svg path {
  fill: #ffffff;
  transition: fill 0.3s ease;
}
.social li a:hover svg path {
  fill: #f39200;
}

.home-video {
  overflow: hidden;
}

.video-background {
  position: relative;
  overflow: hidden;
  width: 100vw;
  height: 100vh;
}
@media (max-width: 900px) {
  .video-background {
    /* height = 100 * (9 / 16) = 56.25 */
    height: 90vw;
  }
}
.video-background iframe {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100vw;
  height: 100vh;
  transform: translate(-50%, -50%);
}
@media (min-aspect-ratio: 16/9) {
  .video-background iframe {
    /* height = 100 * (9 / 16) = 56.25 */
    height: 56.25vw;
  }
}
@media (max-aspect-ratio: 16/9) {
  .video-background iframe {
    /* width = 100 / (9 / 16) = 177.777777 */
    width: 177.78vh;
  }
}
@media (max-width: 900px) {
  .video-background iframe {
    /* height = 100 * (9 / 16) = 56.25 */
    width: 160vw;
  }
}

.wysiwyg > *:last-child {
  margin-bottom: 0;
}
.wysiwyg--narrow-p p {
  max-width: 690px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}